import React from 'react';

const DisplayCrypto = ({ cryptoData, crypto }) => (
  <div className="cryptoContent" key={cryptoData.name}>
    <h2>{cryptoData.name}</h2>
    <p>
      Price :
      <span>{cryptoData.price}</span>
    </p>
    <p>
      Quantity :
      <span>{cryptoData.nb}</span>
    </p>
    <p>
      My Wallet : 
      <span>{Math.floor(cryptoData.nb*cryptoData.price*1000)/1000}€</span>
    </p>
  </div>
);

export default DisplayCrypto;
