import './App.css';
import React, { useEffect, useState } from 'react';
import './components/DisplayCrypto';
import DisplayCrypto from './components/DisplayCrypto';


//HANDLE RANDOM 1 : volatility
let cryptoName = [
  {name : "ImacCoin", price : 1, nb : 1, purchaseNumber : 0, sellNumber : 0, volatily : 0.7}, 
  {name : "EsipeToken", price : 0.10, nb : 1, purchaseNumber : 0, sellNumber : 0, volatily : 0.8}, 
  {name : "EiffelGold", price : 10, nb : 1, purchaseNumber : 0, sellNumber : 0, volatily: 0.3},
  {name : "TicketBoisson", price : 1, nb : 1, purchaseNumber : 0, sellNumber : 0, volatily: 0.1}];
  
let cryptoFeesTab = [
  {name : "ImacCoin", duration : 0, promotion : 0},
  {name : "EsipeToken", duration : 0, promotion : 0},
  {name : "EiffelGold", duration : 0, promotion : 0},
  {name : "TicketBoisson", duration : 0, promotion : 0}];

let twitterUser = ["ElonMask","SuperCrypto","BallGites","AliveJobs","MichaelNotSoDead","LeChevalier","Anonymous","LudwigLeSang","MrHomoSapiens","StupidFinance","Roubliard","LeGot","PipoEtPipou","LaBretagneCestPasOuf"];
let twitterMessage = [
  {effect : "+", amount : 0.2, part1 : "J'adore ", part2 : ""},
  {effect : "-", amount : -0.2, part1 : "Je ne comprends pas l'engouement autour du ", part2 : ""},
  {effect : "+", amount : 0.3, part1 : "Le ", part2 : " est une révolution"},
  {effect : "-", amount : -0.3, art1 : "Dans ma vie j'en ai vu des shitcoins, mais alors le ", part2 : " c'est le boss final"},
  {effect : "all+", amount : 0.2, part1 : "Les cryptos sont la révolution du XXI eme siècle ", part2 : ""},
  {effect : "all-", amount : -0.5, part1 : "La fête est finie ", part2 : ""},
]

//HANDLE RANDOM 2 : probabilité des evenements majeurs 
let probabilityEvent = 1;

let worldNews = [
  {effect : "+", amount : "10", probability: ".10", message : "Pénurie de cartes graphiques, le prix des crypto Augmente !"},
  {effect : "+", amount : "30", probability: ".01", message : "Teslooo considère les cryptomonnaies comme l'avenir de l'économie"},
  {effect : "-", amount : "50", probability: ".03", message : "Enorme Krach des cryptomonnaies "},
  {effect : "-", amount : "20", probability: ".10", message : "Fin de la bull run, grosse correction des prix"},
  {effect : "-", amount : "90", probability: ".01", message : "Piratage du principal exchange, la confiance des marché est perdu"},
]

let resultLotteryTab = [
  {name : "0", amount : 0},
  {name : "1", amount : 0},
  {name : "2", amount : 0},
  {name : "3", amount : 0},
  {name : "4", amount : 0},
  {name : "5", amount : 0},
  {name : "6", amount : 0},
  {name : "7", amount : 0},
  {name : "8", amount : 0},
  {name : "9", amount : 0},
  {name : "10", amount  : 0},
  {name : "11", amount  : 0},
  {name : "12", amount  : 0},
  {name : "13", amount  : 0},
  {name : "14", amount  : 0},
  {name : "15", amount  : 0},
  {name : "16", amount  : 0},
  {name : "17", amount  : 0},
  {name : "18", amount  : 0},
  {name : "19", amount  : 0},
  {name : "20", amount  : 0},
]

let resultVariationTokenTab = [
  {name:"ImacCoin", min:1, max:1, stonk:0, stonkNb:0, unstonk:0, unstonkNb:0 },
  {name: "EsipeToken", min:1, max:1, stonk:0, stonkNb:0, unstonk:0, unstonkNb:0},
  {name: "EiffelGold", min:1, max:1, stonk:0, stonkNb:0, unstonk:0, unstonkNb:0},
  {name: "TicketBoisson", min:1, max:1, stonk:0, stonkNb:0, unstonk:0, unstonkNb:0}];

//HANDLE RANDOM 3 : drop de la lotterie
let lotteryDrop = 0.45;

//HANDLE RANDOM 4 : chance du joueur (compris entre ]0;2[ )
let luck = 1;

let variationPrixImacCoin = 1;

export const App = () => {

  const twitterRandom = (list) => {
    let size = list.length;
    //LOI UNIFORME
    let index = Math.floor(Math.random() * (size));
    //
    return(list[index]);
  }

  const twitterRandomName = (list) => {
    let size = list.length;
    let index = Math.floor(Math.random() * (size));
    return(list[index].name);
  }

  const [crypto, setCrypto] = useState(cryptoName);
  const [cryptoFees, setCryptoFees ] = useState(cryptoFeesTab);
  const [gold, setGold] = useState(1000);
  const [day, setDay] = useState(1);
  const [facture, setFacture] = useState(1000);
  const [displayTwitterUser, setTwitterUser] = useState(twitterRandom(twitterUser));
  const [displayTwitterMessage, setTwitterMessage] = useState(twitterRandom(twitterMessage));
  const [displayTwitterCrypto, setTwitterCrypto] = useState(twitterRandomName(crypto));
  const [displayWorldNews, setWorldNews] = useState([{message : "Rien de nouveau aujourd'hui", amount : 0, effect : "="}]);
  const [lotteryPrice, setLotteryPrice] = useState(10);
  const [baseFee, setBaseFee] = useState(10);
  const [resultLottery, setResultLottery] = useState(resultLotteryTab);
  const [resultLotteryAverage, setResultLotteryAverage] = useState(0);
  const [resultLotteryAverageNb, setResultLotteryAverageNb] = useState(0);
  const [resultVariation, setResultVariation] = useState(resultVariationTokenTab);

  let cryptoAmountSell = 0; 

// PRIX des cryptos
  const handleCoinPurchase = (e, n) => {
    let tempPrice;
    let tempNb;
    let tempPurchaseNumber;
    let tempSellNumber;
    let tempVolatily;
    crypto.filter(e => (e.name === n)).map( (elt) => {
      tempPrice = elt.price;
      tempNb = parseInt(elt.nb);
      tempPurchaseNumber = parseInt(elt.purchaseNumber);
      tempSellNumber = parseInt(elt.sellNumber);
      tempVolatily = elt.volatily;
    })
    if(tempPurchaseNumber<=(parseInt(gold)/parseInt(tempPrice))){
      tempNb+= tempPurchaseNumber;
      setCrypto([...crypto.filter(e => (e.name !== n)),{name : n, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber, sellNumber : tempSellNumber, volatily : tempVolatily}]);
      setGold(gold-(tempPurchaseNumber*parseInt(tempPrice)));
    }
    else{
      tempNb+= (parseInt(gold)/parseInt(tempPrice))
      setCrypto([...crypto.filter(e => (e.name !== n)),{name : n, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber, sellNumber : tempSellNumber, volatily : tempVolatily}]);
      setGold(gold-((parseInt(gold)/parseInt(tempPrice))*parseInt(tempPrice)));
    }
    //setCrypto([...crypto.filter(e => (e.name !== n)),{name : n, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber}]);
  }

  const handleCoinSell = (e, n, promotion) => {
    let tempPrice;
    let tempNb;
    let tempPurchaseNumber;
    let tempSellNumber;
    let tempVolatily;
    crypto.filter(e => (e.name === n)).map( (elt) => {
      tempPrice = elt.price;
      tempNb = parseInt(elt.nb);
      tempPurchaseNumber = parseInt(elt.purchaseNumber);
      tempSellNumber = parseInt(elt.sellNumber);
      tempVolatily = elt.volatily
    })
    if(tempSellNumber<=tempNb){
      tempNb-= tempSellNumber;
      tempSellNumber -= tempSellNumber*( (baseFee-promotion)/100 ) 
      setCrypto([...crypto.filter(e => (e.name !== n)),{name : n, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber, sellNumber : tempSellNumber, volatily : tempVolatily}]);
      setGold(gold+(tempSellNumber*parseInt(tempPrice)));
    }
    else{
      setGold(gold+parseInt(tempPrice)*parseInt(tempNb));
      tempNb = 0;
      setCrypto([...crypto.filter(e => (e.name !== n)),{name : n, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber, sellNumber : tempSellNumber, volatily : tempVolatily}]);
      
    }
  }

  const handleAmountPurchase = (e, n) => {
    //console.log(e.target.value);
    let tempPrice;
    let tempNb;
    let tempPurchaseNumber;
    let tempSellNumber;
    let tempVolatily;
    crypto.filter(e => (e.name === n)).map( (elt) => {
      tempNb = parseInt(elt.nb);
      tempPrice = elt.price;
      tempPurchaseNumber = parseInt(e.target.value);
      tempSellNumber = elt.sellNumber;
      tempVolatily = elt.volatily;
    })
    setCrypto([...crypto.filter(e => (e.name !== n)),{name : n, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber, sellNumber : tempSellNumber, volatily : tempVolatily}]);
  }

  const handleAmountSell = (e,n) => {
    //console.log(e.target.value);
    cryptoAmountSell = e.target.value;
    //console.log(e.target.value);
    let tempPrice;
    let tempNb;
    let tempPurchaseNumber;
    let tempSellNumber;
    let tempVolatily;
    crypto.filter(e => (e.name === n)).map( (elt) => {
      tempNb = parseInt(elt.nb);
      tempPrice = elt.price;
      tempSellNumber = parseInt(e.target.value);
      tempPurchaseNumber = elt.purchaseNumber;
      tempVolatily = elt.volatily;
    })
    setCrypto([...crypto.filter(e => (e.name !== n)),{name : n, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber, sellNumber : tempSellNumber, volatily : tempVolatily}]);
  }

  const Gauss = (x) =>{
    return ( (1/(0.4*Math.sqrt(2*Math.PI)))*Math.exp(((-1/2)*((x/0.4)*(x/0.4)))));
  }

  const Normal = (x,esperance,ecart_type) => { //LOI NORMALE
    let es = parseFloat(esperance);
    let ec = parseFloat(ecart_type);
    let res = (1/(ec*Math.sqrt(2*Math.PI))*Math.exp((-1/2)*Math.pow((x-es)/ec,2)));
    return res;
  }

  const updatePrice = (c, tab) => {
    let random;
    let signe; 
    let tempPrice;
    let tempName = c.name;
    let tempNb = c.nb;
    let tempPurchaseNumber = 0;
    let tempSellNumber = 0;
    let tempVolatily = c.volatily;
    let tempEvolution;
    // Prix en hausse ou en baisse
    signe= Math.random();
    (signe > 0.5)? signe = signe * 1 : signe = signe * -1;
    // Random adapté au signe et à la volatilité de la crypto
    random= Math.random();
    // loi normale 
    random = Normal(random, 0, 0.15)
    // Modification du random en fonction des alertes twitter
    if( (displayTwitterMessage.effect==="all+") || (displayTwitterMessage.effect==="all-") ){
      random = (random + displayTwitterMessage.amount)*tempVolatily;
    }
    else{
      if(displayTwitterCrypto === tempName){
        random = (random + displayTwitterMessage.amount)*tempVolatily;
      }
      else{
        random = random * tempVolatily;
      }
    }
    if(random>1){
      random = 1.0;
    }
    if(random<-1){
      random = -1.0;
    }

    //modification du prix en passant le random dans une courbe de Gauss //PROSTITUTION EN LOI NORMALE
    //corrélation la variable du signe influence la variation des prix issue d'une loi normale
    random = (signe*(1-Gauss(random)));
    tempEvolution = parseFloat(c.price);
    tempPrice = c.price + random * c.price;
    tempPrice = Math.floor(tempPrice*1000)/1000;
    tempEvolution = parseFloat(tempPrice)/parseFloat(tempEvolution);
    //modification du prix par evenement 
    displayWorldNews.map( e => {
      if(e.effect === "="){
        tempPrice = tempPrice * 1; 
      }
      else{
        if(e.effect === "+"){
          tempPrice =  tempPrice + tempPrice/100*e.amount * luck;
        }
        else{
          if(luck <= 1){
            tempPrice = tempPrice - tempPrice/100*e.amount * luck;
          }
          else{
            tempPrice = tempPrice - tempPrice/100*e.amount * (1-luck);
          }
        }
      }
    })
    //tempEvolution = parseFloat(tempPrice)/parseFloat(tempEvolution);
    let data = {name : tempName, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber, sellNumber : tempSellNumber, volatily : tempVolatily};
    tab.push(data);
    // modification du prix de la lotterie
    if(c.name == "ImacCoin"){
      if(tempEvolution >= 1.30){
        variationPrixImacCoin = 1-((tempEvolution-1)/3);
      }
      else{
        variationPrixImacCoin = 1;
      }
      setLotteryPrice(Math.floor(tempPrice * 10));
    }

    //MAJ de la partie statistique
    let temp = resultVariation;
    for(let i=0; i<temp.length; i++){
      if(temp[i].name == tempName){
        if(temp[i].min > tempEvolution){
          temp[i].min = parseFloat(tempEvolution);
        }
        if(temp[i].max < tempEvolution){
          temp[i].max = tempEvolution;
        }
        if(tempEvolution < 1){
          temp[i].unstonk += tempEvolution;
          temp[i].unstonkNb++;
        }
        else{
          temp[i].stonk += tempEvolution;
          temp[i].stonkNb++;
        }
      }
    }
    setResultVariation(temp);
  }

  const handleNextDay = () => {
    
    if(day===30){
      setDay(1);
      setGold(gold-1000);
      if(gold-1000<0){
        let temp = document.getElementById("loseScreen");
        console.log(temp);
        temp.classList.add("loseDisplay");
      }
    }
    else{
      setDay(day+1);
    }
    let tab = [];
    //console.log("===================== Crypto Twitter : "+displayTwitterCrypto+"  || impact : "+displayTwitterMessage.amount);
    worldNewsRandom();
    crypto.forEach((c) => {
      updatePrice(c, tab);
    });
    setTwitterUser(twitterRandom(twitterUser));
    setTwitterMessage(twitterRandom(twitterMessage));
    setTwitterCrypto(twitterRandomName(crypto));
    setCrypto(tab); 
    setCryptoFees(feesCalculator());  
    
  }

  const worldNewsRandom = () => {
    let random;
    let tabNews = [];
    worldNews.map( e => {
      random= Math.random();
      //LOI DE BERNOULLI
      if (random <= (e.probability * probabilityEvent))
        tabNews.push({message : e.message, amount : e.amount, effect : e.effect});
      })
      //
    if (tabNews.length === 0)
      tabNews.push({message : "Rien de nouveau aujourd'hui", amount : 0, effect : "="});
    setWorldNews(tabNews);
  }

  const geometrique = (p,k) => {
    var res = p*Math.pow((1-p),(k-1));
    return res;
  }

  const handleLottery = (e, n) => {
    if(gold >= lotteryPrice){
      setGold(gold-lotteryPrice);
      let tempPrice;
      let tempNb;
      let tempPurchaseNumber;
      let tempSellNumber;
      let tempVolatily;
      crypto.filter(e => (e.name === n)).map( (elt) => {
        tempPrice = elt.price;
        tempNb = parseInt(elt.nb);
        tempPurchaseNumber = parseInt(elt.purchaseNumber);
        tempSellNumber = parseInt(elt.sellNumber);
        tempVolatily = elt.volatily;
      })
      //LOI BINOMIALE 
      let p = lotteryDrop; // probabilité d'avoir un token
      let nbToken = 0;
      for(let i = 0; i<20; i++){
        if((Math.random()*luck*variationPrixImacCoin)>(1-p)){
          nbToken++;
        }
      }
      let tempStat = resultLottery;
      tempStat[nbToken].amount++;
      let temp = resultLotteryAverage+nbToken;
      setResultLottery(tempStat);
      setResultLotteryAverage(temp);
      temp = resultLotteryAverageNb+1;
      setResultLotteryAverageNb(temp);

      tempNb+= nbToken;
      //
      setCrypto([...crypto.filter(e => (e.name !== n)),{name : n, price : tempPrice, nb : tempNb, purchaseNumber : tempPurchaseNumber, sellNumber : tempSellNumber, volatily : tempVolatily}]);
    }
  }

  //LOI GEOMETRIQUE
  const feesCalculator = () =>{
    let tab = [];
    let random = 0;
    let duration = 1;
    cryptoFees.map( elt => {
      //console.log(elt);
      if(elt.duration === -1){
        random = Math.random();
        if(random <= 0.1){
          duration = 1;
        }
        else{
          let temp = random;
          duration = 1;
          while(temp>=0.1){
            duration++;
            temp = geometrique(temp, duration);
            //console.log(temp);
          }
        }
        let promotion = Math.random();
        tab.push({name : elt.name, duration : duration, promotion : promotion});
      }
      else{
        duration = elt.duration;
        let promotion; 
        if(duration == 0){
          promotion = 0;
          duration--;
        }
        else{
          duration--;
          promotion = Math.random();
        }
        tab.push({name : elt.name, duration : duration, promotion : promotion});
      }
    })
    //console.log(tab);
    return tab;
    
  }

  return (
    <div className="App">
      <div id="header">
        <div id="gold">GOLD : <b>{gold}€</b></div>
        <div id="day" >day : <b>{day}</b>/30</div>
        <div> Facture à payer le premier du mois : <b>{facture}€</b></div>
      </div>
      <button id="nextDay" onClick = {(event) => handleNextDay()}>Jour suivant</button>
      <div className="CryptoContainer">
          {crypto.sort((a, b) => a.name.localeCompare(b.name)).map( (cryptoData) => (
          <div className="CryptoData">
            <DisplayCrypto
            cryptoData = {cryptoData}
            cryptoBDD = {crypto} 
            />
            <div>
              <p> Fees :  
                <span>
                  {
                    cryptoFees.filter((a) => a.name == cryptoData.name).map( (b) => ( Math.floor(baseFee - (Math.floor(b.promotion*100)/10)) ) )
                  }
                </span>
              </p>
            </div>
            <div>
              <p>Prix d'achat : <span className="greenColored">{cryptoData.purchaseNumber*cryptoData.price}</span></p>
              <input type="number" min = {1} placeholder={"Montant"} onChange={(event) => handleAmountPurchase(event, cryptoData.name)}></input>
              <button onClick = {(event) => handleCoinPurchase(event,cryptoData.name)}>Acheter</button>
            </div>
            <div>
              {
                cryptoFees.filter((a) => a.name == cryptoData.name).map( (b) => ( 
                  <>
                    <p>Prix de vente : <span className="redColored">{cryptoData.sellNumber*cryptoData.price}</span></p>
                    <p>Frais : <span className="redColored">{cryptoData.sellNumber*cryptoData.price*((10-b.promotion)/100)}</span></p>
                    <p>Total : <span className="redColored">{(cryptoData.sellNumber*cryptoData.price)-(cryptoData.sellNumber*cryptoData.price*((baseFee-b.promotion)/100))} </span></p>
                    <input type="number" placeholder={"Montant"} onChange={(event) => handleAmountSell(event, cryptoData.name)}></input>
                    <button onClick = {(event) => handleCoinSell(event,cryptoData.name,cryptoAmountSell,b.promotion)}>Vendre</button>
                  </>
                ))
              }   
            </div>
          </div>
          ))}
      </div>
      <div className="CryptoData">
        <h2> La lotterie de l'IMAC COIN</h2>
        <p> Price : {lotteryPrice}€ </p>
        <button onClick = {(event) => handleLottery(event,"ImacCoin")}>Acheter Ticket</button>
      </div>
      {displayWorldNews.map( (e) => (
        <div id="WorldNews">
          <p>La chaine info : </p>
          <p> {e.message}</p>
        </div>
      ))}
      <div className="CryptoTweet">
        <div id="twitterUser">@{displayTwitterUser}</div>
        {
          ((displayTwitterMessage.effect === "all+") || (displayTwitterMessage.effect === "all-")) ? <div> {displayTwitterMessage.part1} </div> : <div>{displayTwitterMessage.part1} {displayTwitterCrypto} {displayTwitterMessage.part2}</div>
        }
      </div>

      <div id="Stats">
        <h1>
          Les statistiques
        </h1>
        <h2>Résultat de la lotterie</h2>
        <div id="StatLottery">
          {
            resultLottery.map( e => (
              <div className="StatLotteryBox">
                <div className="StatLotteryBoxName">{e.name}</div>
                <div>{e.amount}</div>
              </div>
            ))
          }
        </div>
        <div>
            <p>Tentatives : {resultLotteryAverageNb}</p>
            <p>Moyenne des tokens : {resultLotteryAverage/resultLotteryAverageNb}</p>
            <p>Résultat théorique : 9 </p>
        </div>
        <h2>Résultat des variations des cours</h2>
        <div id="StatVariation">
          {
            resultVariation.map( e => (
              <div className="StatVariationBox">
                <div className="StatLotteryBoxName">
                  <h2>{e.name}</h2>
                  <p> Variation Moyenne des cours </p>
                  <div>
                    <p>Hausse : {(e.stonk/e.stonkNb-1)*100}%</p>
                    <p>Baisse : {(1-(e.unstonk/e.unstonkNb))*100}%</p>
                  </div>
                  <p> Résultat le plus fort </p>
                  <div>
                    <p>Hausse : {(e.max-1)*100}%</p>
                    <p>Baisse : {(1-e.min)*100}%</p>
                  </div>
                </div>
                
              </div>
            ))
          }
        </div>
      </div>

      <div id="loseScreen">
          <h1>
            Zut tu es ruiné·e !
          </h1>
      </div>

    </div>
  );
}

export default App;
